import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { formatGram, getUserdata, localStoragedecryp } from "../../../../../components/helpers/function";
class LaporanBatalPembelianExel extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    let total_berat = 0;
    let total_berat_bruto = 0;
    let total_harga_rata = 0;
    let total_harga_nota = 0;
    let total_barang = 0;
    let harga = 0;

    this.props.data.forEach((barang, index) => {
      barang.detail.forEach((item, index) => {
        total_berat = total_berat + item.berat;
        total_berat_bruto = total_berat_bruto + item.berat_nota;
        total_harga_rata = total_harga_rata + item.harga_rata;
        total_harga_nota = total_harga_nota + item.harga_nota;
        harga = harga + item.harga || 0;
      });
      total_barang = total_barang + barang.detail.length;
    });
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan Batal Pembelian"
          sheet="Laporan Batal Pembelian"
          buttonText="Export Exel"
        />
        <table
          rowkey="kode_member"
          id="table-to-xls"
          style={{ display: "none" }}
        >
          <thead>
            <tr>
              <th colSpan="11"> LAPORAN BATAL PEMBELIAN </th>
            </tr>
            <tr>
              <th colSpan="11">
              Periode
                {" "}
                {localStoragedecryp("tgl_laporan").length === 0
                  ? null
                  : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
                    " s/d " +
                    JSON.parse(localStoragedecryp("tgl_laporan"))
                      .tgl_akhir}{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="11"> TOKO :  {getUserdata().nama_toko} </th>
            </tr>
           
            <tr>
              <th colSpan="11"> ALAMAT :  {getUserdata().alamat_toko} </th>
            </tr>
            <tr>
              <th colSpan="11"></th>
            </tr>
            <tr>
              <th
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  
                }}
              >
                Tanggal
              </th>
              <th
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  
                }}
              >
                Kode Sales
              </th>
              <th
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  
                }}
              >
                No Faktur Beli
              </th>
              <th
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  
                }}
              >
                Nama Customer
              </th>
             
              <th
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  
                }}
              >
                NO HP
              </th>
              <th
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  
                }}
              >
                NAMA BARANG
              </th>
              <th
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  
                }}
              >
                BERAT
              </th>
              <th
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  
                }}
              >
                BERAT NOTA
              </th>
              <th
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  
                }}
              >
                HARGA RATA
              </th>
              <th
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  
                }}
              >
                HARGA NOTA
              </th>
              <th
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  
                }}
              >
                HARGA BELI
              </th>
            </tr>
          </thead>
          <tbody>
            {this.props.data.map((item, index) => (
              <>
                {/* <tr>
                  <th> No Faktur Batal </th>
                  <th colSpan="13" style={{ textAlign: "left" }}>
                    {" "}
                    {item._id.no_faktur_batal}{" "}
                  </th>
                </tr> */}

                {item.detail.map((row, index) => (
                  <>
                    <tr>
                      <td>
                        {" "}
                        {row.tgl_system}
                      </td>
                      <td>
                        {" "}
                        {row.kode_sales}
                      </td>
                      <td>
                        {" "}
                        {row.no_faktur_beli}
                      </td>
                      <td>
                      {row.nama_customer}
                      </td>
                      <td>
                      {row.no_hp}
                      </td>
                      
                      <td
                      >
                        {" "}
                        {row.nama_barang}
                      </td>
                      <td
                        style={{
                          
                          textAlign: "right",
                        }}
                      >
                        {" "}
                        {formatGram(row.berat,3)}
                      </td>
                      <td
                        style={{
                          
                          textAlign: "right",
                        }}
                      >
                        {" "}
                        {formatGram(row.berat_nota,3)}
                      </td>
                      <td
                        style={{
                          
                          textAlign: "right",
                        }}
                      >
                        {row.harga_rata.toLocaleString("kr-KO")}
                      </td>
                      <td
                        style={{
                          
                          textAlign: "right",
                        }}
                      >
                        {row.harga_nota.toLocaleString("kr-KO")}
                      </td>
                      <td
                        style={{
                          
                          textAlign: "right",
                        }}
                      >
                        {row.harga === undefined ? 0 : row.harga.toLocaleString("kr-KO")}
                      </td>
                    </tr>
                  </>
                ))}
                <tr>
                  <th
                    style={{
                      backgroundColor: "#E8E5E5",
                      
                      color: "#000",
                      textAlign: "right",
                    }}
                    colSpan="6"
                  >
                    {" "}
                    Sub Total :
                  </th>
                  <th
                    style={{
                      backgroundColor: "#E8E5E5",
                      color: "#000",
                      textAlign: "right",
                    }}
                  >
                    {" "}
                    {formatGram(item.detail
                      .map((list) => list.berat)
                      .reduce((a, b) => a + b, 0),3)}{" "}
                  </th>
                  <th
                    style={{
                      backgroundColor: "#E8E5E5",
                      color: "#000",
                      textAlign: "right",
                    }}
                  >
                    {" "}
                    {formatGram(item.detail
                      .map((list) => list.berat_nota)
                      .reduce((a, b) => a + b, 0),3)}{" "}
                  </th>
                  <th
                    style={{
                      backgroundColor: "#E8E5E5",
                      color: "#000",
                      textAlign: "right",
                    }}
                  >
                 {item.detail
                      .map((list) => list.harga_rata)
                      .reduce((a, b) => a + b, 0).toLocaleString("kr-KO")}{" "}
                  </th>
                  <th
                    style={{
                      backgroundColor: "#E8E5E5",
                      color: "#000",
                      textAlign: "right",
                    }}
                  >
                 {item.detail
                      .map((list) => list.harga_nota)
                      .reduce((a, b) => a + b, 0).toLocaleString("kr-KO")}{" "}
                  </th>
                  <th
                    style={{
                      backgroundColor: "#E8E5E5",
                      color: "#000",
                      textAlign: "right",
                    }}
                  >
                 {item.detail
                      .map((list) => list.harga || 0)
                      .reduce((a, b) => a + b, 0).toLocaleString("kr-KO")}{" "}
                  </th>
                 
                </tr>
              </>
            ))}
          </tbody>
          <tfoot>
           
          </tfoot>
        </table>
      </>
    );
  }
}
export default LaporanBatalPembelianExel;
