import jsPDF from "jspdf";
import "jspdf-autotable";
import {
  formatGram,
  getToday,
  getUserdata,
  localStoragedecryp,
} from "../../../../../components/helpers/function";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanBatalPeembelianPdf = (data = "") => {
  // initialize jsPDF
  const doc = new jsPDF("L", "mm");
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let footRows = [];
  let tableColumn = [];
  let finalY = 30;
  let total_semua = 0;


  let grand_total_berat = 0;
  let grand_total_berat_bruto = 0;
  let grand_total_hargaa_rata = 0;
  let grand_total_harga_nota = 0;

  doc.setFontSize(15);
  doc.text("LAPORAN BATAL PEMBELIAN", 14, 15);
  doc.setFontSize(20);
  doc.text(getUserdata().nama_toko, 200, 15);
  doc.setFontSize(8);
  let jml_alamat = getUserdata().alamat_toko.length;
  if (jml_alamat > 20) {
    doc.text(getUserdata().alamat_toko.slice(0,48), 200, 20);
  }
  if (jml_alamat > 50) {
    doc.text(getUserdata().alamat_toko.slice(48,90), 200, 25);
  }
  doc.setFontSize(10);
  doc.setProperties({
    title: "LAPORAN BATAL PEMBELIAN",
  });
  //row 1
  doc.text(
    `PERIODE : ${
      localStoragedecryp("tgl_laporan").length === 0
        ? null
        : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
          " s/d " +
          JSON.parse(localStoragedecryp("tgl_laporan")).tgl_akhir
    }`,
    14,
    25
  );
  tableColumn = [
    [
      {
        content: `Tanggal`,
      },
      {
        content: `Kode Sales`,
      },
      {
        content: `No Faktur Beli`,
      },
      {
        content: `Nama Customer`,
      },
      {
        content: `No Hp`,
      },
   
      {
        content: `Nama Barang`,
      },
      {
        content: `Berat`,
       styles: {
            halign: "right",
          },
      },
      {
        content: `Berat Nota`,
       styles: {
            halign: "right",
          },
      },
      {
        content: `Harga Rata`,
       styles: {
            halign: "right",
          },
      },
      {
        content: `Harga Nota`,
       styles: {
            halign: "right",
          },
      },
      {
        content: `Harga Beli`,
       styles: {
            halign: "right",
          },
      },
     
    ],
  ];
  let harga_beli = 0;
  let total_berat = 0;
  let total_berat_bruto = 0;
  let total_hargaa_rata = 0;
  let total_harga_nota = 0;
  data.forEach((item, index) => {
    // console.log(item._id.pembayaran[0].jenis);
    // let head = [
    //   {
    //     content: `Kategori : -`,
    //     colSpan: 13,
    //   },
    // ];

    // tableRows.push(head);
    item.detail.forEach((item, index) => {
      let rows = [
        {
          content: `${item.tgl_system}`,
          styles: {
            cellWidth : 20
          },
        },
        item.kode_sales,
        item.no_faktur_beli,
        item.nama_customer,
        item.no_hp,
        // item.alamat_customer,
        // item.kode_gudang,
        // item.kode_dept,
        item.nama_barang,
        {
          content: `${formatGram(item.berat,3)}`,
          styles: {
            halign: "right",
          },
        },
        {
          content: `${formatGram(item.berat_nota,3)}`,
          styles: {
            halign: "right",
          },
        },
        {
          content: `${item.harga_rata.toLocaleString("kr-KO")}`,
          styles: {
            halign: "right",
          },
        },
        {
          content: `${item.harga_nota.toLocaleString("kr-KO")}`,
          styles: {
            halign: "right",
          },
        },
        {
          content: `${item.harga=== undefined ? 0 :item.harga.toLocaleString("kr-KO")}`,
          styles: {
            halign: "right",
          },
        },
        
        // item.berat_nota,
        // parseInt(item.harga_rata).toLocaleString("kr-KO"),
        // parseInt(item.harga_nota).toLocaleString("kr-KO"),
        // item.kondisi,
      ];

      total_berat = total_berat + item.berat;
      total_berat_bruto = total_berat_bruto + item.berat_nota;
      total_hargaa_rata = total_hargaa_rata + item.harga_rata;
      total_harga_nota = total_harga_nota + item.harga_nota;
      harga_beli = harga_beli + item.harga || 0;
     
      grand_total_berat = grand_total_berat + item.berat;
      grand_total_berat_bruto = grand_total_berat_bruto + item.berat_nota;
      grand_total_hargaa_rata = grand_total_hargaa_rata + item.harga_rata;
      grand_total_harga_nota = grand_total_harga_nota + item.harga_nota;
      tableRows.push(rows);
    });

    total_semua = total_semua + item.detail.length;

   
  });
  let footer = [
    {
      content: `Subtotal :`,
      colSpan: 6,
      styles: {
        halign: "right",
       fillColor: "#E8E5E5",
      textColor: "#000",
      },
    },
    {
      content: `${formatGram(total_berat,3)}`,
      styles: {
       fillColor: "#E8E5E5",
        textColor: "#000",
        halign: "right",
      },
    },
    {
      content: `${formatGram(total_berat_bruto,3)}`,
      styles: {
       fillColor: "#E8E5E5",
        textColor: "#000",
        halign: "right",
      },
    },
    {
      content: `${total_hargaa_rata.toLocaleString("kr-KO")}`,
      styles: {
       fillColor: "#E8E5E5",
        textColor: "#000",
      halign: "right",
      
      },
    },
    {
      content: `${total_harga_nota.toLocaleString("kr-KO")}`,
      styles: {
       fillColor: "#E8E5E5",
        textColor: "#000",
      halign: "right",
      
      },
    },
    {
      content: `${harga_beli.toLocaleString("kr-KO")}`,
      styles: {
       fillColor: "#E8E5E5",
        textColor: "#000",
      halign: "right",
      
      },
    },
    // {
    //   content: `${parseInt(total_hargaa_rata).toLocaleString("kr-KO")}`,
    //   styles: {
    //    fillColor: "#E8E5E5",
    //   textColor: "#000",
    //   },
    // },
    // {
    //   content: `${parseInt(total_harga_nota).toLocaleString("kr-KO")}`,
    //   styles: {
    //    fillColor: "#E8E5E5",
    //   textColor: "#000",
    //   },
    // },
    // {
    //   content: ``,
    //   styles: {
    //    fillColor: "#E8E5E5",
    //   textColor: "#000",
    //   },
    // },
  ];
  tableRows.push(footer);
  // let grand = [
  //   {
  //     content: `Grand Total :`,
  //     colSpan: 4,
  //     styles: {
  //       halign: "right",
  //      fillColor: "#E8E5E5",
  //       textColor: "#000",
  //     },
  //   },
  //   {
  //     content: `${grand_total_berat.toFixed(3)}`,
  //     styles: {
  //      fillColor: "#E8E5E5",
  //       textColor: "#000",
  //     },
  //   },
  //   {
  //     content: ``,
  //     styles: {
  //      fillColor: "#E8E5E5",
  //       textColor: "#000",
  //     },
  //   },
  //   // {
  //   //   content: `${parseInt(grand_total_hargaa_rata).toLocaleString("kr-KO")}`,
  //   //   styles: {
  //   //    fillColor: "#E8E5E5",
  //   //     textColor: "#000",
  //   //   },
  //   // },
  //   // {
  //   //   content: `${parseInt(grand_total_harga_nota).toLocaleString("kr-KO")}`,
  //   //   styles: {
  //   //    fillColor: "#E8E5E5",
  //   //     textColor: "#000",
  //   //   },
  //   // },
  //   // {
  //   //   content: ``,
  //   //   styles: {
  //   //    fillColor: "#E8E5E5",
  //   //     textColor: "#000",
  //   //   },
  //   // },
  // ];
  // tableRows.push(grand);
  let printed = [
    {
      colSpan:9,
      content: `Printed By ${JSON.parse(localStoragedecryp("userdata")).user_id} / ${getToday()}`,
      styles: {
        // lineWidth: 0.1,
        fontStyle: "italic",
        textColor: "#000",
        // fillColor: "#E8E5E5"
      },
    },
  ]
  tableRows.push(printed);
  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    foot: footRows,
    startY: finalY,
    theme: "plain",
    rowPageBreak: "avoid",
    pageBreak: "avoid",
    margin: { top: 10 },
    footStyles: {
      lineWidth: 0.02,
      lineColor: "#000",
      fontSize: 8,
    },
    bodyStyles: {
      // lineWidth: 0.02,
      // lineColor: "#000",
      fontSize: 8,
    },
    headStyles: {
      fontSize: 8,
      // lineWidth: 0.02,
      // lineColor: "#000",
      fillColor: "#E8E5E5",
      textColor: "#000",
    },
  });
  finalY = doc.autoTableEndPosY() + 3;
  tableRows = [];
  footRows = [];
  // total_daftar = 0;
  // total_berat = 0;
  // total_berat_bruto = 0;
  // total_hargaa_rata = 0;
  // total_harga_nota = 0;
  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left
  // doc.text(
  //   `User                          : ${
  //     JSON.parse(localStoragedecryp("userdata")).user_id
  //   }`,
  //   14,
  //   finalY + 10
  // );
  // doc.text(`Cetak                        : ${getToday()}`, 14, finalY + 16);
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  // doc.autoPrint();
  // doc.save(`LAPORAN BATAL PEMBELIAN.pdf`);
  var string = doc.output("datauristring");
  // var embed = "<embed width='100%' height='100%' src='" + string + "'/>";
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN BATAL PEMBELIAN</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // x.document.write(embed);
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanBatalPeembelianPdf;
