import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { InputDate } from "../../../../components/helpers/field";
import { formatGram, getTglSystem } from "../../../../components/helpers/function";
import Tabel from "../../../../components/helpers/tabel";
import LaporanBatalPembelianExel from "./exel/LaporanBatalPembelianExel";
import LaporanBatalPeembelianPdf from "./pdf/LaporanBatalPeembelianPdf";
// import Loading from 'react-fullscreen-loading';

class HeadFormBatalBeli extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
    };
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  handleSubmit(data) {
    console.log(data);
  }
  exportPdf() {
    LaporanBatalPeembelianPdf(this.props.datapembelian)
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }
  render() {
    let databarang = [];
    let berat = 0;
    let berat_nota = 0;
    let harga_rata = 0;
    let harga_nota = 0;
    let harga_beli = 0;
    this.props.datapembelian.forEach((element) => {
      element.detail.forEach((detail) => {
        databarang.push(detail);
        berat = berat + detail.berat;
        berat_nota = berat_nota + detail.berat_nota;
        harga_rata = harga_rata + detail.harga_rata;
        harga_nota = harga_nota + detail.harga_nota;
        harga_beli = harga_beli + detail.harga || 0;
      });
    });
    const columns = [
      {
        dataField: "kode_sales",
        text: "Kode Sales",
        footerAttrs: {
          colSpan: "4",
        },
        footer: (cell, row, num, index) => {
          return <div>Total Barang : {databarang.length} </div>;
        },
      },
      {
        dataField: "nama_customer",
        text: "Nama Customer",
      },
      {
        dataField: "no_hp",
        text: "No Hp",
      },
      {
        dataField: "nama_barang",
        text: "Nama Barang",
      },
      {
        dataField: "berat",
        text: "Berat",
        headerClasses: "text-right",

        formatter: (cell) => {
          return <div className="text-right"> {formatGram(cell, 3)} </div>;
        },
        footer: (columnData) => {
          return <div className="text-right"> {formatGram(columnData.reduce((acc, item) => acc + item, 0), 3)} </div>
        }
      },
      {
        dataField: "berat_nota",
        text: "Berat Nota",
        headerClasses: "text-right",
        formatter: (cell) => {
          return <div className="text-right"> {formatGram(cell, 3)} </div>;
        },
        footer: (columnData) => {
          return <div className="text-right"> {formatGram(columnData.reduce((acc, item) => acc + item, 0), 3)} </div>
        }
      },
      {
        dataField: "harga_rata",
        text: "Harga Rata",
        headerClasses: "text-right",

        footer: (cell, row, num, index) => {
          return (
            <div className="text-right">{harga_rata.toLocaleString("kr-KO")} </div>
          );
        },
        formatter: (cell) => {
          return (
            <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
          );
        },
      },
    
      {
        dataField: "harga_nota",
        text: "Harga Nota",
        headerClasses: "text-right",
        footer: (cell, row, num, index) => {
          return (
            <div className="text-right">
              {harga_nota.toLocaleString("kr-KO")}{" "}
            </div>
          );
        },
        formatter: (cell) => {
          return (
            <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
          );
        },
      },
      {
        dataField: "harga",
        text: "Harga Beli",
        headerClasses: "text-right",
        footer: (cell, row, num, index) => {
          return (
            <div className="text-right">
              {harga_beli.toLocaleString("kr-KO")}{" "}
            </div>
          );
        },
        formatter: (cell) => {
          return (
            <div className="text-right">{cell === undefined ? 0 :  cell.toLocaleString("kr-KO")}</div>
          );
        },
      },
    ];
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
      <div className="row">
        <div className="col-lg-4">
          <Field
            name="tgl_awal"
            component={InputDate}
            label="Tanggal Dari"
            type="text"
            selected={this.state.tgl_awal}
            onChange={(date) => this.setStartDate(date)}
            placeholder="Masukan Tanggal Dari"
          />
        </div>

        <div className="col-lg-4">
          <Field
            name="tgl_akhir"
            component={InputDate}
            type="text"
            selected={this.state.tgl_akhir}
            onChange={(date) => this.setLastDate(date)}
            label="Tanggal Akhir"
            placeholder="Masukan Tanggal Akhir"
          />
        </div>

        <div className="col-lg-4">
          <div className="text-right">
            <label>&nbsp;</label>
            <button
              type="submit"
              value="PDF"
              name="type_btn"
              className="btn btn-primary btn-block"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Mencari
                </>
              ) : (
                "Cari Laporan"
              )}
            </button>
          </div>
          </div>
          <div className="col-lg-12">
            <Tabel
              keyField="no_hp"
              data={this.props.export ? databarang : []}
              columns={columns}
              empty={true}
              textEmpty="Data Laporan Batal Pembelian Kosong"
            />
          </div>
        <div className={this.props.export ? "col-lg-12" : "col-lg-12 d-none"}>
          {/* <label htmlFor="">&nbsp;</label> */}
          <div className="row">
            <div className="col-lg-6">
              <button
                type="button"
                onClick={() => this.exportPdf()}
                className="btn btn-warning btn-block"
              >
                {" "}
                Export PDF{" "}
              </button>
            </div>
            <div className="col-lg-6">
              <LaporanBatalPembelianExel data={this.props.datapembelian} />
            </div>
          </div>
        </div>
      </div>
       {/* {/* {this.state.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null} */}
    </form>
    );
  }
}

HeadFormBatalBeli = reduxForm({
  form: "HeadFormBatalBeli",
  enableReinitialize: true,
})(HeadFormBatalBeli);
// export default connect()(HeadFormBatalBeli);
export default connect((state) => {
  return {
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadFormBatalBeli);
